<template lang="pug">
  #erd(:style="`width: ${CANVAS_WIDTH}px; height: ${CANVAS_HEIGHT}px;`")
    canvas-menu
    canvas-grid
    quick-menu
    .canvas
      canvas-main
      canvas-svg
</template>

<script>
import CanvasMenu from '@/components/editor/CanvasMenu'
import CanvasGrid from '@/components/editor/CanvasGrid'
import QuickMenu from '@/components/editor/QuickMenu'
import CanvasMain from '@/components/editor/CanvasMain'
import CanvasSvg from '@/components/editor/CanvasSvg'
import ERD from '@/js/editor/ERD'

export default {
  name: 'ERD',
  components: {
    CanvasMenu,
    CanvasGrid,
    QuickMenu,
    CanvasMain,
    CanvasSvg
  },
  computed: {
    CANVAS_WIDTH () {
      return ERD.store().state.CANVAS_WIDTH
    },
    CANVAS_HEIGHT () {
      return ERD.store().state.CANVAS_HEIGHT
    }
  },
  destroyed () {
    //  
    ERD.destroy()
  }
}
</script>

<style lang="scss">
  #erd {
    display: flex;
    z-index: 2;
    background-color: #282828;

    .canvas {
      display: flex;
    }
  }

  /*reset css*/
  /*   */
  * {
    margin: 0;
    padding: 0;
  }

  /*  body    */
  h1, h2, h3, h4, h5, h6 {
    font-size: 100%;
  }

  /*()   */
  ul, ol {
    list-style: none;
  }

  /*  */
  a {
    text-decoration: none;
  }

  /*  img    */
  img {
    border: none;
  }

  /*      */
  table {
    border-collapse: collapse;
    border-spacing: 0;
  }

  /*()    1 =  12px*/
  body {
    font-family: 'Noto Sans', sans-serif;
    font-size: 12px;
    line-height: 1;
  }

  input {
    border: none;
  }

  *:focus {
    outline: none;
  }

  body {
    overflow-x: hidden;
    overflow-y: hidden;
  }
  /* width */
  body::-webkit-scrollbar {
    width: 8px;
    height: 8px;
  }
  /* Track */
  body::-webkit-scrollbar-track {
    background: #fafbfe;
    border-left: 1px solid  #e4e6e6;
  }
  /* Handle */
  body::-webkit-scrollbar-thumb {
    background: #aaa;
  }
  /* Handle : hover*/
  body::-webkit-scrollbar-thumb:hover {
    background: white;
  }

  /* width */
  textarea::-webkit-scrollbar {
    width: 6px;
    height: 6px;
  }
  /* Track */
  textarea::-webkit-scrollbar-track {
    background: #191919;
    border-left: 1px solid  #191919;
  }
  /* Handle */
  textarea::-webkit-scrollbar-thumb {
    background: #aaa;
  }
  /* Handle : hover*/
  textarea::-webkit-scrollbar-thumb:hover {
    background: white;
  }
</style>
